.horizontal-menu{
    list-style-type: none;
    margin: 0;
    padding: 0;
    background: #444;
    text-align: center;
    font-size: calc(5px + 2vmin);
}
.main-nav{
    /* min-width: 100vh; */
    position: sticky;
    top: 0;
    z-index: 100;

}
.horizontal-menu li{
    display: inline-block;
}
.horizontal-menu div{
    color: #fff;
    margin: 16px;
    display: block;
    cursor: pointer;
}
