.container-about{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    /* color: #C58940; */
    font-size: calc(4px + 2vmin);
    /* font-size: 2vmin; */
}
.sub-container-about{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    /* font-weight: bold; */
}
.data-about{
    text-align: left;
    padding: 0 40px;
}
.sub-other-skill{
    font-size: calc(1px + 2vmin);
    padding: 0 30px;
    translate: calc(5vmin) 0;
}
.head-other-skill{
    margin: 0;
}