.container-project{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    /* color: #C58940; */
    font-size: calc(4px + 2vmin);

}
section{
    width: 70%;
    height: 20%;
    margin: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.project{
    border: 5px #aaa solid;
    /* border: 5px #C58940 solid; */
    border-radius: 3vmin;
    width: 35%;
    height: 100%;
    margin: 10px;
    transition-duration: 0.5s;
}
.project:hover {
    box-shadow: 0 0 25px rgba(255, 255, 255, 0.4);
    /* box-shadow: var(--box-shadow); */
    /* box-shadow: 0 0 25px rgba(33,33,33,.2);  */
}
.icon-project{
    color: white;
    /* color: #C58940; */
    font-size: calc(10px + 6vmin);
    margin-bottom: 2vmin;
}
.project-link{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: white;
    /* color: #C58940; */
}